import React, { Component } from 'react';
import { Route, Switch } from "react-router-dom";
import { TweenLite, TweenMax }  from "gsap";
import { TransitionGroup, Transition } from "react-transition-group";
import 'gsap/src/uncompressed/plugins/ScrollToPlugin'
import Home from "./Home"
import Work from "./Work"
import Article from "./Article"

class Routes extends Component {
  constructor(props) {
    super(props);

    this.state = {
      backToTop: false
    };
    this.toScroll = this.toScroll.bind(this);
  }

  toScroll(e, location) {
    e.preventDefault();
    TweenLite.to(window, 1.2, {scrollTo: location});
  }

  render() {
    const completeCall = target => {
      TweenLite.set(target, { clearProps: "y" });
    };
    return (
      <TransitionGroup>
        <Transition
          key={this.props.location.pathname}
          timeout={500}
          appear={true}
          mountOnEnter={true}
          unmountOnExit={true}
          onEnter={node => {
              // first kill all tweens of the target
              TweenMax.killTweensOf(node);
              TweenLite.set(node, {
                y: 100,
                autoAlpha: 0
              });
              // animate in the element
              TweenLite.to(node, 1, {
                delay: 1,
                autoAlpha: 1,
                y: 0,
                onComplete: completeCall,
                onCompleteParams: [node]
              });
            }} // on enter end

            onExit={node => {
                // first kill all tweens of the target
                TweenMax.killTweensOf(node);
                // animate out the element
                TweenLite.to(node, 0.3, {
                  opacity: 0,
                  y: 100
                });
            }} // on exit end
            >
          <Switch location={this.props.location}>
            <Route exact path="/" render={props => <Home {...props}/> }/>
            <Route path="/Work" render={props => <Work {...props}/> }/>
            <Route path="/Article" render={props => <Article {...props}/> }/>
          </Switch>
          </Transition>
        </TransitionGroup>
    );
  }
}

export default Routes;



