import React, { Component } from 'react';
import { TweenLite, TweenMax }  from "gsap";
import { TransitionGroup, Transition } from "react-transition-group";

class Canvas extends Component {
    constructor(props) {
        super(props);
        this.state = {
            lineWidth: 150,
            t:1,
            tcolor:0,
        };
				this.animateLine = this.animateLine.bind(this)
        this.handleScroll = this.handleScroll.bind(this);
        this.color = this.color.bind(this);
        this.red = this.red.bind(this);
        this.green = this.green.bind(this);
        this.blue = this.blue.bind(this);
			}
			handleScroll(event) {
        this.setState({ tcolor: this.state.tcolor + 0.120})
        const canvas = document.getElementById("canvas")
				const ctx = canvas.getContext("2d")
				const vpW = window.innerWidth;
        const vpH = window.innerHeight;
        ctx.clearRect(0, 0, vpW, vpH);
        canvas.height = vpH;
        canvas.width = vpW;
        ctx.beginPath();
        ctx.lineWidth = this.state.lineWidth;
        var grd=ctx.createLinearGradient(0, 0, 10, 0);
        grd.addColorStop(0, this.color());
				grd.addColorStop(1,this.color());

				ctx.strokeStyle = grd;
        ctx.moveTo(vpW, 0);
        ctx.lineTo(0, vpH);
        ctx.stroke();
			};
    componentDidMount() {
				this.animateLine()
				document.getElementById("wrapper").addEventListener('scroll', this.handleScroll);
		};

		componentWillUnmount() {
			document.getElementById("wrapper").removeEventListener('scroll', this.handleScroll);
		};

    animateLine() {
				const canvas = document.getElementById("canvas")
				const ctx = canvas.getContext("2d")
        const vpW = window.innerWidth;
        const vpH = window.innerHeight;
        ctx.clearRect(0, 0, vpW, vpH);
        canvas.height = vpH;
        canvas.width = vpW;
        ctx.beginPath();
        ctx.lineWidth = this.state.t;



        ctx.moveTo(vpW, 0);
        ctx.lineTo(0, vpH);
        var grd=ctx.createLinearGradient(0, 0, vpW, 0);
        grd.addColorStop(0, this.color());
        grd.addColorStop(0.5, this.color());
				ctx.strokeStyle = grd;
        ctx.stroke();

				this.setState({ t: this.state.t + 1.5})

				if(this.state.t < this.state.lineWidth){
					window.requestAnimationFrame(this.animateLine);
				}
    }
    red(x, y, t) {
      return( Math.floor(192 + 64*Math.cos( (x*x-y*y)/300 + t )) );
    }

    green(x, y, t) {
      return( Math.floor(192 + 64*Math.sin( (x*x*Math.cos(t/4)+y*y*Math.sin(t/3))/300 ) ) );
    }

    blue(x, y, t) {
      return( Math.floor(192 + 64*Math.sin( 5*Math.sin(t/9) + ((x-100)*(x-100)+(y-100)*(y-100))/1100) ));
    }

    color() {
      for(var x=0;x<=35;x++) {
        for(var y=0;y<=35;y++) {
          return `rgb(${this.red(x, y, this.state.tcolor)}, ${this.green(x, y, this.state.tcolor)}, ${this.blue(x, y, this.state.tcolor)})`
        }
      }
    }

    render() {
        const divStyle = {
            height: '100vh',
            left: '0',
            position: 'fixed',
            top: '0',
            width: '100vw',
            zIndex: '-100',
        };
        return(
          <TransitionGroup>
						<Transition
							timeout={500}
							appear={true}
							mountOnEnter={true}
							unmountOnExit={false}
							onEnter={node => {
									// first kill all tweens of the target
									TweenMax.killTweensOf(node);
									TweenMax.killTweensOf(node);
									TweenLite.set(node, {
										autoAlpha: 0
									});
									// animate in the element
									TweenLite.to(node, 1, {
										delay: 1,
										autoAlpha: 0.3,
										onCompleteParams: [node]
									});
								}} // on enter end
						>
            	<canvas id={"canvas"} style={divStyle}></canvas>
            </Transition>
          </TransitionGroup>
        )
    }
}

  export default Canvas




