import React, { Component } from 'react';
import styles from "./Intro.scss"

class Intro extends Component {
  render() {
    return (
        <div className={styles.intro}>
            <div className="container">
                <div className="row">
                    <div className={styles.wrapper}>
                        <p className="para-large">Hey there, i'm Adam Wright, a developer with a creative mindset currently living in London.
                        I have worked with the likes of <a href="https://www.superunion.com/" target="_blank" rel="noopener noreferrer">Brand Union</a>,
                        <a href="https://exposuredigital.com/" target="_blank" rel="noopener noreferrer">Exposure Digital</a>, and <a href="http://www.syndicut.com/" target="_blank" rel="noopener noreferrer">Syndicut</a></p>
                    </div>
                </div>
            </div>
        </div>
    );
  }
}

export default Intro;



