import React, { Component } from 'react';
import TweenLite  from "gsap";
import 'gsap/src/uncompressed/plugins/ScrollToPlugin'
import styles from "./scrolltop.scss"

class ScrollTop extends Component {
    constructor(props) {
        super(props);
        this.state = {
          backToTop: false
        };
        this.handleScroll = this.handleScroll.bind(this);
      }

      handleScroll(event) {
        let fromTop =  document.getElementById("wrapper").scrollTop

        if(fromTop > 300) {
          this.setState({backToTop: true})
        }
        else {
          this.setState({backToTop: false})
        }
      };

      scrollTop() {
        TweenLite.to("#wrapper", 1.2, {scrollTo: 0});
      }

      componentDidMount() {
        document.getElementById("wrapper").addEventListener('scroll', this.handleScroll);
      };

      componentWillUnmount() {
        document.getElementById("wrapper").removeEventListener('scroll', this.handleScroll);
      };
  render() {
    return (
        <div className={`${styles.scrolltop} ${this.state.backToTop ? styles['is-visible'] : ''}`} onClick={this.scrollTop}>Scroll shsh</div>
    );
  }
}

export default ScrollTop;


