import React, { Component } from 'react';
import { Link } from 'react-router-dom'
import styles from "./WorkListing.scss"
import penderyn from "./resources/images/penderyn-store.jpg"
import exposure from "./resources/images/exposure.jpg"
import fortitude from "./resources/images/sky-fortitude.jpg"
import sonovate from "./resources/images/sonovate.jpg"
import panoramic from "./resources/images/panoramic-journey.jpg"

class WorkListing extends Component {
  render() {
    return (
        <div className={styles.worklisting} id={this.props.id}>
            <div className="container">
                <div className="row">
                    <div className={styles.headercontainer}>
                        <h2 className="heading">Work</h2>
                    </div>
                </div>
                <div className="row">
                    <div className={styles.items}>
                        <Link to="https://www.sonovate.com/" target="_blank" className={styles.item}>
                            <div className={styles.itemimagecontainer}>
                                <img src={sonovate} className={styles.itemimage} alt="Sonovate" />
                            </div>
                            <div className={styles.itemcontent}>
                                <h3 className="heading-small">Sonovate</h3>
                            </div>
                        </Link>
                        <Link to="https://exposuredigital.com/" target="_blank" className={[styles.item, styles.itemlarge].join(' ')}>
                            <div className={styles.itemimagecontainer}>
                                <img src={exposure} className={styles.itemimage} alt="Exposure Digital" />
                            </div>
                            <div className={styles.itemcontent}>
                                <h3 className="heading-small">Exposure Digital</h3>
                            </div>
                        </Link>
                        <Link to="http://www.visitfortitude.com/" target="_blank" className={[styles.item, styles.itemlarge].join(' ')}>
                            <div className={styles.itemimagecontainer}>
                                <img src={fortitude} className={styles.itemimage} alt="Visit Fortitude" />
                            </div>
                            <div className={styles.itemcontent}>
                                <h3 className="heading-small">Visit Fortitude</h3>
                            </div>
                        </Link>
                        <Link to="https://www.penderynstore.com/" target="_blank" className={styles.item}>
                            <div className={styles.itemimagecontainer}>
                                <img src={penderyn} className={styles.itemimage} alt="Pendreyn Store" />
                            </div>
                            <div className={styles.itemcontent}>
                                <h3 className="heading-small">Pendreyn Store</h3>
                            </div>
                        </Link>
                        <Link to="https://www.panoramicjourneys.com/" className={styles.item}>
                            <div className={styles.itemimagecontainer}>
                                <img src={panoramic} className={styles.itemimage} alt="Panoramic Journeys" />
                            </div>
                            <div className={styles.itemcontent}>
                                <h3 className="heading-small">Panoramic Journeys</h3>
                            </div>
                        </Link>
                    </div>
                </div>
            </div>
        </div>
    );
  }
}

export default WorkListing;



