import React, { Component } from 'react';
import styles from "./Contact.scss"

class Contact extends Component {
  render() {
    return (
        <div className={styles.contact} id={this.props.id}>
            <div className="container">
                <div className="row">
                    <div className={styles.headercontainer}>
                        <h2 className="heading">Contact</h2>
                    </div>
                </div>
                <div className="row">
                    <div className={styles.content}>
                        <p>I am always interested in hearing about new projects<br/><br/>
                        <a href="mailto:adam.wright90@yahoo.co.uk">adam.wright90@yahoo.co.uk</a></p>
                    </div>
                </div>
            </div>
        </div>
    );
  }
}

export default Contact;



