import React, { Component } from 'react';
import { withRouter } from 'react-router';
import { Link } from 'react-router-dom';
import { TweenLite, TweenMax }  from "gsap";
import { TransitionGroup, Transition } from "react-transition-group";
import styles from './Header.scss';

class Header extends Component {

    render() {
      return (
        <TransitionGroup>
        <Transition
            timeout={500}
            appear={true}
            mountOnEnter={true}
            unmountOnExit={true}
            onEnter={node => {
                // first kill all tweens of the target
                TweenMax.killTweensOf(node);
                TweenLite.set(node, {
                    y: -30,
                    autoAlpha: 0
                  });
                // animate in the element
                TweenLite.to(node, 1.5, {
                delay: 1,
                y: 0,
                autoAlpha: 1,
                onCompleteParams: [node]
                });
            }} // on enter end
            onExit={node => {
                // first kill all tweens of the target
                TweenMax.killTweensOf(node);
                // animate out the element
                TweenLite.to(node, 0.3, {
                  autoAlpha: 0,
                  y: -10
                });
            }} // on exit end
        >
          <div>
            <a href="#main-content" className="u-visually-hide">Skip to content</a>
            <nav className={`${styles.navbar} js-navigation`}>
                <div className="container">
                    <div className="row">
                        <div className={styles.wrapper}>
                            <Link to="/" className={styles.brand}>Adam Wright</Link>
                            {this.props.location.pathname === "/" && (
                                <div className={styles.links}>
                                    <ul className={styles.list}>
                                        <li className={styles.item}><a href="" className={styles.link} onClick={(e) => this.props.toScroll(e, "#about")}>About</a></li>
                                        <li className={styles.item}><a href="" className={styles.link} onClick={(e) => this.props.toScroll(e, "#work")}>Work</a></li>
                                        <li className={styles.item}><a href="" className={styles.link} onClick={(e) => this.props.toScroll(e, "#contact")}>Contact</a></li>
                                    </ul>
                                </div>
                            )}
                        </div>
                    </div>
                </div>
            </nav>
        </div>
        </Transition>
        </TransitionGroup>
        )
    }
}

export default withRouter(Header);


