import React, { Component } from 'react';
import { BrowserRouter as Router, Route} from "react-router-dom";
import { TweenLite, TweenMax }  from "gsap";
import { TransitionGroup, Transition } from "react-transition-group";
import 'gsap/src/uncompressed/plugins/ScrollToPlugin'
import Header from "./Header"
import ScrollTop from "./ScrollTop"
import AppRoutes from "./Routes"
import Canvas from "./Canvas"

class App extends Component {
  constructor(props) {
    super(props);
    this.state = {
      backToTop: false,
      loading: true
    };

    this.toScroll = this.toScroll.bind(this);
  }

  toScroll(e, location) {
    e.preventDefault();
    console.log("dldldl");
    TweenLite.to("#wrapper", 1.2, {scrollTo: location});
  }

  componentDidMount() {
    setTimeout(() => this.setState({ loading: false }), 1500); // simulates an async action, and hides the spinner
  }

  render() {
    const divStyle = {
      border: '0px solid #cccccc',
      height: '100vh',
      left: '0px',
      position: 'fixed',
      top: '0',
      width: '100vw',
      overflowY: 'auto',
      zIndex: '-1',
    };
    const { loading } = this.state;

    if(loading) { // if your component doesn't have to wait for an async action, remove this block
      return null; // render null when app is not ready
    }
    return (
      <Router>
        <div>
        <TransitionGroup>
          <Transition
          timeout={500}
          appear={true}
          mountOnEnter={true}
          unmountOnExit={false}
          onEnter={node => {
              // first kill all tweens of the target
              TweenMax.killTweensOf(node);
              TweenLite.set(node, {
                borderWidth: 0,
                onCompleteParams: [node]
              });
              // animate in the element
              TweenLite.to(node, 1, {
                borderWidth: 10,
                onCompleteParams: [node]
              });
            }} // on enter end
          >
            <div id={"wrapper"} style={divStyle}>
              <Header toScroll={this.toScroll}></Header>
              <Route path="/" render={props => <AppRoutes {...props}/> }/>
              <ScrollTop className={this.props.className} onClick={this.scrollTop}></ScrollTop>
            </div>
          </Transition>
        </TransitionGroup>
        <Canvas></Canvas>
        </div>
      </Router>
    );
  }
}

export default App;



